<template>
  <div class="scene" id="scene" ref="toTop" @scroll="handleScroll">
    <skeleton v-if="skeletonLoading" :nums="3"></skeleton>
    <div v-else>
      <Header :leftArrow="!$route.query.browerFirstIn">
        <div v-if="showShare"  class="share-btn" slot="right" :style="{backgroundImage: `url(${$globalIcon.blackShareIcon})`}" @click="doShare"></div>
      </Header>
      <div class="scene-component" ref="sceneComponent">
        <van-sticky :offset-top="'.92rem'">
          <div class="program-tab" style="padding-left: .24rem" v-if="tabNeed.length">
            <span
              v-for="(item, index) in tabNeed"
              :class="{'active': index == curNeedIndex, 'program-tab--disable': (item.tabId && !isSpace) ? !tabNeedCheck.includes(item.tabId) : false}"
              :key="item.tabId + index"
              @click="changeTab(item.tabId, index, 1, (item.tabId && !isSpace) ? !tabNeedCheck.includes(item.tabId) : false)"
            >{{item.tabName}}</span>
          </div>
          <div class="program-tab" style="padding-left: .24rem" v-if="tabSpace.length">
            <span
              v-for="(item, index) in tabSpace"
              :class="{'active': index == curSpaceIndex ,'program-tab--disable': (item.spaceCode && !isTab) ? !tabSpaceCheck.includes(item.spaceCode) : false}"
              :key="item.spaceCode + index"
              @click="changeTab(item.spaceCode, index, 2, (item.spaceCode && !isTab) ? !tabSpaceCheck.includes(item.spaceCode) : false)"
            >{{item.spaceName}}</span>
          </div>
        </van-sticky>
        <div style="height: .92rem"></div>
        <van-list
          :immediate-check="false"
          :finished="finished"
          v-model="loading"
          :offset="10"
          van-clearfix
          @load="onLoad">
          <div class="sence-item-warp">
            <div class="program-product-item" v-for="(prod, index) in sceneList" :key="index" @click="goPage(prod.planDetailsRelativeUrl, false, true)">
              <div class="program-product-img">
                <div class="program-play" v-if="!prod.hasVideo"><span></span></div>
                <vanImg :src="prod.planThumbImg | imageToHttps" />
              </div>
              <p>{{prod.salesHighlights}}</p>
            </div>
          </div>
        </van-list>
        <div class="no-more" v-if="finished && sceneList.length !== 0">没有更多啦~</div>
        <common-empty v-if="finished && sceneList.length === 0"/>
        <!-- 返回顶部 -->
        <top-feedBack :isShow="showToTopFlag" @hiddenFeedBack="hiddenFeedBack" @toTop="fnScrollToTop"></top-feedBack>
      </div>
    </div>
  </div>
</template>
<script>
import skeleton from '@/components/skeleton'
import Header from '@/components/header'
// import scenceComponent from './components/scene-component'
import { mapGetters } from 'vuex'
import {
  getStoreSceneTabs,
  getStoreScene,
  validateTabOrSpace
} from '@/http/service/shop'
import commonEmpty from '@/views/shopDetail/component/commonEmpty';
import mixins from '@/components/top-feedBack/mixins_inpage_scroll.js'

// import { GetSceneList, getProgramLivesInfo } from '@/http/service/scene'
export default {
  mixins: [mixins],
  components: {
    skeleton,
    Header,
    // sceneDetail,
    // scenceComponent,
    commonEmpty,
  },
  data() {
    return {
      skeletonLoading: true,
      isEdit: false,
      active: 0,
      detailList: [],
      data: { data: { tabs: [] } },
      testData: {},
      storeId: '',
      autoFold: false,
      liveIds: [], // 直播id数组
      liveInfo: {}, // 直播信息
      showShare: this.$util.isHaierApp() || this.$util.isSgRn(),
      loading: true,
      finished: false,
      tabNeed: [], // 需求
      tabSpace: [], // 空间
      sceneList: [],
      curNeedIndex: 0,
      curSpaceIndex: 0,
      pageIndex: 1,
      tabSpaceCheck: [],
      tabNeedCheck: [],
      isSpace: true,
      isTab: true,
      tabNeedCheckAll: [],
      tabSpaceCheckAll: [],
      scrollTarget: '#scene'
    }
  },
  created() {
    this.storeId = this.$route.query.storeId;
    if (this.storeId) {
      this.$route.meta.title = '推荐智慧场景';
      document.title = '推荐智慧场景';
    }
    console.log(this.title);
    this.showShare = (this.$util.isHaierApp() || this.$util.isSgRn()) && this.storeId;
    // this.getSceneList();
  },
  mounted() {
    this.getTabs();
    this.$nextTick(i => {
      let pageInfo = this.getBrowserInterfaceSize()
      document.getElementById('scene').style.height = (pageInfo.pageHeight + 1) + 'px'
    })
  },
  computed: {
    ...mapGetters(['mId']),
  },
  methods: {
    // /**
    //  * 返回顶部
    // */
    // fnScrollToTop() {
    //   this.$refs.sceneComponent.scrollIntoView({
    //     block: 'start',
    //     behavior: 'smooth', // 平滑过渡
    //   }) // 回到顶部
    // },

    /**
     * 获取浏览器窗口实际宽高
    */
    getBrowserInterfaceSize() {
      var pageWidth = window.innerWidth;
      var pageHeight = window.innerHeight;

      if (typeof pageWidth !== 'number') {
        // 在标准模式下面
        if (document.compatMode === 'CSS1Compat') {
          pageWidth = document.documentElement.clientWidth;
          pageHeight = document.documentElement.clientHeight;
        } else {
          pageWidth = document.body.clientWidth;
          pageHeight = window.body.clientHeight;
        }
      }

      return {
        pageWidth: pageWidth,
        pageHeight: pageHeight
      }
    },
    /**
     * 场景获取直播信息
     */
    programLiveInfo() {
      // getProgramLivesInfo(this.liveIds).then(res => {
      //   let result = res.data;
      //   console.log(result, '+++++++')
      //   this.liveInfo = result
      // })
    },
    doShare() {
      let url = location.href.indexOf('?') !== -1 ? location.href + '&shareId=' + this.mId : location.href + '?shareId=' + this.mId
      console.log('分享Url:', url);
      this.$util.inAppShare({
        url,
      })
    },
    onLoad() {
      this.pageIndex++;
      this.getSceneList();
    },
    changeTab(code, index, type, disable) {
      if (disable) {
        return
      }
      if (type === 1) { // 需求
        if (this.curNeedIndex !== index) {
          this.validateTabOrSpace({
            tabId: code,
            isTab: !code
          })
        }
        this.curNeedIndex = index;
      } else { // 空间
        if (this.curSpaceIndex !== index) {
          this.validateTabOrSpace({
            spaceCode: code,
            isSpace: !code
          })
        }
        this.curSpaceIndex = index;
      }
      this.pageIndex = 1;
      this.finished = false;
      this.loading = true;
      this.getSceneList();
    },
    getTabs() {
      getStoreSceneTabs({ storeId: this.storeId }).then(res => {
        if (res.success) {
          this.tabNeed = (res.data.tab || [])
          this.tabSpace = (res.data.space || [])
          if (this.tabNeed.length) {
            this.tabNeed.unshift({
              tabId: '',
              tabName: '需求'
            });
            this.tabNeedCheck = this.tabNeedCheckAll = this.tabNeed.map(i => {
              return i.tabId
            })
          } if (this.tabSpace.length) {
            this.tabSpace.unshift({
              spaceCode: '',
              spaceName: '空间'
            });
            this.tabSpaceCheck = this.tabSpaceCheckAll = this.tabSpace.map(i => {
              return i.spaceCode
            })
          }
          if (this.tabNeed.length || this.tabSpace.length) {
            this.getSceneList();
          } else {
            this.loading = false;
            this.finished = true;
          }
        } else {
          this.loading = false;
          this.finished = true;
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
      });
    },
    getSceneList() {
      const param = {
        currentPage: this.pageIndex,
        pageNum: 10,
        tabId: this.curNeedIndex === 0 ? '' : this.tabNeed[this.curNeedIndex].tabId,
        spaceCode: this.curSpaceIndex === 0 ? '' : this.tabSpace[this.curSpaceIndex].spaceCode,
        storeId: this.storeId,
      };
      getStoreScene(param).then(res => {
        console.log('场景列表：', res);
        this.skeletonLoading = false;
        this.loading = false;
        if (res.success) {
          if (this.pageIndex === 1) {
            this.sceneList = []
          }
          if (this.pageIndex > 1) {
            this.sceneList = [...this.sceneList, ...res.data];
          } else {
            this.sceneList = res.data;
          }
          if (res.data.length < 10) {
            this.loading = false;
            this.finished = true;
          }
        } else {
          this.finished = true;
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
      });
    },
    goPage(link) {
      // this.$router.push('/scene')
      const curLink = this.storeId ? `${link}&storeId=${this.storeId}` : link;
      if (curLink.indexOf('http') !== -1) {
        let url = curLink.split('/sgmobile')[1]
        console.log('url1:', `${url}`);
        this.$router.push(`${url}`)
      } else {
        this.$router.push(`${curLink}`)
        console.log('url2:', curLink);
      }
    },
    validateTabOrSpace ({
      spaceCode,
      tabId,
      isSpace,
      isTab
    }) {
      validateTabOrSpace({
        storeId: this.storeId,
        spaceCode: spaceCode || '',
        tabId: tabId || ''
      }).then(res => {
        this.isSpace = false
        this.isTab = false
        if (spaceCode) {
          this.tabNeedCheck = res.data.tabs
        } else if (tabId) {
          this.tabSpaceCheck = res.data.spaces
        } else if (isSpace) {
          this.isSpace = true
          this.tabNeedCheck = this.tabNeedCheckAll
        } else if (isTab) {
          this.isTab = true
          this.tabSpaceCheck = this.tabSpaceCheckAll
        }
      })
    }
  },
}
</script>
